export default {
  name: 'LandingPage',
  layout: 'blank',
  async asyncData({$axios, error}) {
    try {
      return await $axios.$get('/nuxt/landing')
    } catch (e) {
      return error(e)
    }
  },
  head: {
    title: 'Belajar, Berbagi, Berkontribusi'
  }
}
